.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;

  @media screen and (min-width: $mobile) {
    width: 278px;
  }

  @media screen and (min-width: $tablet) {
    width: 704px;
  }

  @media screen and (min-width: $desktop) {
    width: 1132px;
  }
}
