.program__container {
  display: flex;
  justify-content: space-between;
}

.program__features {
  width: 390px;
}

.program__title {
  font-size: 36px;
  line-height: 1.81;
  margin-bottom: 8px;
} 

.program__list {
  padding: 20px 0 16px 0;
}

.program__item {
  margin-top: 16px;
}

.content__list {
  max-width: 500px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.content__img {
  position: absolute;
  top: -25%;
  left: 25%;
  cursor: pointer;
  transition: transform 1200ms;   
}

.content__item {
  width: 130px;
  height: 130px;
  padding-top: 50px;
  position: relative;
  text-align: center;
  border-radius: 50%;
  background-color:#FEF3E4;
  box-shadow: 0px 4px 4px rgba(171, 129, 108, 0.25);
  cursor: pointer;
  transition: background-color 1000ms, transform 1000ms, box-shadow 1000ms;
  
  @media screen and(min-width:$tablet) {
      width: 152px;
      height: 152px;
      padding-top: 53px;
    }
  
    @media screen and(min-width:$desktop) {
      width: 180px;
      height: 180px;
      padding-top: 65px;
    }
}

// .content__item:nth-child(2n+4) {
//   margin-top: 116px;
// }

.content__item:hover {
  background-color: #FFE7C9;
  transform: scale(1.05);
  box-shadow: 0px 8px 8px rgba(171, 129, 108, 0.25);
}

.content__item:hover .content__img {
  transform: rotate(180deg);
}

.content__title {
  font-size: 18px;
  line-height: 1.22;

  @media screen and(min-width:$desktop) {
    font-size: 24px;
    line-height: 1.25;
  }
}

.content__text {
  margin-top: 4px;
  font-size: 14px;
  line-height: 1.21;

  @media screen and(min-width:$tablet) {
    margin-top: 14px;
  }

  @media screen and(min-width:$desktop) {
    margin-top: 16px;
    font-size: 16px;
    line-height: 1.22;
  }
}

.content__icon {
  margin-top: 8px;
  width: 19px;
  height: 19px;
  
  @media screen and(min-width:$tablet) {
    width: 21px;
    height: 21px;
  }

  @media screen and(min-width:$desktop) {
    width: 25px;
    height: 25px;
  }
}
